

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 24px;
  padding-right: 24px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.modal {
  width: 408px;
  height: 586px;
  background-color: #fff;
  border-radius: 4px;
  padding-left: 24px;
  padding-right: 24px;
  position: absolute;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.signin-form, .login-form {
  flex-direction: column;
  display: flex;
}

.signin-form.hidden, .login-form.hidden, .logout.hidden {
  display: none;
}

.profile-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}











